@import url('https://fonts.googleapis.com/css2?family=Golos+Text:wght@600&display=swap');
.Candidate{
    display: flex;

    width: 91.4%;
    max-height: 460px;
    background-color: #111111;
    border-radius: 20px;

    margin: 0 auto 1.42%;
}

.text{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 10px;

    width: 78.125%;
    height: 79px;
}

.text a{
    text-decoration: none;
}

.photo{
    box-sizing: border-box;

    width: 100px;
    height: 100px;

    border: 2px solid #ADFF00;
    border-radius: 10px;

    margin: 20px;
}

.name{
    min-width: 193px;
    max-height: 29px;

    font-family: 'Golos Text', sans-serif;
    font-size: 24px;
    line-height: 120%;

    color: #FFFFFF;

    margin-bottom: 0;
}

.vk{
    cursor: pointer;

    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;

    width: 126px;
    height: 33px;

    border: none;
    border-radius: 10px;

    background: #ADFF00;
    padding: 11px 7.2px 12px 7.2px;
}

.vk h5{
    margin: 0;
    font-family: 'Golos Text', sans-serif;
    font-weight: 600;
    font-size: 18px;
    line-height: 120%;

    color: #1E1E1E;
}

.date{
    display: flex;
    align-items: flex-start;

    width: 196px;
    height: 43px;
}

.date h4{
    width: 136px;
    height: 43px;

    font-family: 'Golos Text', sans-serif;
    font-weight: 600;
    font-size: 36px;
    line-height: 120%;

    color: #FFFFFF;
}

/*320px*/
@media screen and (max-width: 1024px) {
    .Candidate {
        position: relative;
        width: 220px;
        height: 320px;
        flex-direction: column;
        margin-bottom: 20px;
    }

    .photo {
        width: 190px;
        height: 190px;
        margin: 49px 15px 15px;
    }

    .text {
        margin: 0 15px 15px 15px;
        width: 190px;
        height: 51px;
    }

    .name {
        margin: 0;
        min-width: 129px;
        max-height: 19px;

        font-weight: 600;
        font-size: 16px;
    }

    .vk {
        width: 88px;
        height: 24px;
    }

    .vk h5 {
        font-size: 12px;
    }
    .date{
        position: absolute;
        top: 15px;
        left: 15px;

        width: 190px;
        height: 19px;
        text-align: center;
    }
    .date h4{
        width: 190px;
        height: 19px;
        margin: 0;
        font-size: 16px;
    }
}